import React from 'react';

const Lost = () => {
    return (
        <div>
            <h1 style={{color: "white"}}>
                You're lost! Get back...get..back..now!!!
            </h1>
            <img style={{ paddingTop: "50px"}} src="/images/biohazard.png" alt="Back"/>
        </div>
    )
}

export default Lost;


